import { LitElement, html } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import screenfull from 'screenfull';

@customElement('young-float-ball')
export class YoungFloatBall extends LitElement {
  /**
   * 默认层级
   * @default 9999
   */
  @property({ type: Number })
  zindex = 9999;
  
  /**
   * 是否旋转
   */
  @property({ type: Boolean })
  private isrotate = false;

  /**
   * 默认点击切换全屏状态
   * 传递此参数则只触发点击事件
   */
  @property({ type: Boolean })
  custom = false;

  /**
   * 布局方向，默认为横向布局
   */
  @property({ type: String })
  direction: 'cross' | 'row' = 'cross';

  /**
   * 顶部偏移距离
   */
  @property({ type: String })
  top = '20px';

  /**
   * 悬浮球隐藏部分偏移值
   */
  @property({ type: String })
  right = '-20px';

  /**
   * 默认图片
   */
  @property({ type: String })
  icon = '//g2021-cdn.laiyouxi.com/image/21Store/laiyouxi_others/ball.webp';

  constructor() {
    super();

    const evt = 'onorientationchange' in window ? 'orientationchange' : 'resize';
    const resize = () => {
      this.changeDirection();
    };

    window.addEventListener(evt, resize, true);
    this.changeDirection();
  }

  isSupport() {
    const ua = navigator.userAgent;

    const UnSupportReg = [
      /HEYTAP/img,
      /PFEM10/img,
      /Baidu/img
    ];

    if (UnSupportReg.some((v) => v.test(ua))) {
      return false;
    } 

    return screenfull.isEnabled;
  }

  changeDirection() {
    setTimeout(() => {
      const { innerWidth: width, innerHeight: height } = window;
      if (width > height) {
        console.log('正常横屏')
        this.isrotate = false;
      } else {
        console.log('竖屏旋转 90°，假装横屏')
        this.isrotate = true;
      }
    }, 100);
  }

  realPosition() {
    // 横版游戏
    if (this.direction == 'cross') {
      if (this.isrotate) {
        return {
          bottom: this.right,
          right: this.top
        };
      } else {
        return {
          right: this.right,
          top: this.top
        };
      }
    } else {
      // 竖版游戏
      if (this.isrotate) {
        return {
          top: this.top,
          right: this.right
        };
      } else {
        return {
          left: this.top,
          top: this.right
        };
      }
    }
  }

  async toggleFullscreen() {
    if (this.custom) {
      // 自定义操作
      return;
    }

    // 切换全屏
    if (this.isSupport()) {
      await screenfull.toggle();
    } else {
      this.dispatchEvent(new CustomEvent('fail'));
    }

    this.changeDirection();
  }

  render() {
    return html`
    <style>
    * {
      margin: 0;
      padding: 0;
    }
    :host {
      font-family: SourceHanSansSC-Medium, SourceHanSansSC;
    }
    #box {
      cursor: pointer;
      position: fixed;
      border-radius: 100%;
      padding: 6px;
      background: rgba(0, 0, 0, 0.5);
      display: flex;
      justify-content: center;
      align-items: center;

      rotate: ${this.isrotate && this.direction === 'cross' ? '90deg' : !this.isrotate && this.direction === 'row' ? '-90deg' : '0'};
      z-index: ${this.zindex};

      left: ${this.realPosition().left};
      right: ${this.realPosition().right};
      top: ${this.realPosition().top};
      bottom: ${this.realPosition().bottom};
    }

    #box-img {
      width: 52px;
      height: 52px;
      border-radius: 100%;
    }
    </style>
    <div
      id="box"
      @click=${this.toggleFullscreen}
    >
      <img id="box-img" loading="lazy" src=${this.icon} alt="float ball" />
    </div>
    `;
  }
}